import { Component, OnInit, ViewChild } from '@angular/core';
import { DataserviceService } from 'src/app/dataservice.service';
import { MatTableDataSource } from '@angular/material/table';
import { MatPaginator } from '@angular/material/paginator';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {

  lastrefreshedtime: Date;
  total: any = { confirmed: '', active: '', recovered: '', deaths: '' };
  changes: any = { confirmed: '', active: '', recovered: '', deaths: '' };

  constructor(private dataservice: DataserviceService) { }


  ngOnInit() {
    this.dataservice.getStateWiseData()
      .subscribe(
        data => {
          this.setBasicData(data);
        }
      )
    this.dataservice.getDailyCaseStatusData()
      .subscribe(
        data => {
          this.setDifferenceData(data);
        }
      )
  }
  setDifferenceData(data: any) {
    let history = data.data.history;
    let lastDay = history.length - 1;
    let count = 1;
    history.forEach(item => {
      if (count == lastDay) {
        this.changes.confirmed = this.total.confirmed - item.total.confirmed;
        this.changes.active = this.total.active - item.total.active;
        this.changes.recovered = this.total.recovered - item.total.recovered;
        this.changes.deaths = this.total.deaths - item.total.deaths;
      }
      count++;
    })
  }
  setBasicData(data: any) {
    this.lastrefreshedtime = data.lastRefreshed;
    this.total.confirmed = data.data.total.confirmed;
    this.total.active = data.data.total.active;
    this.total.recovered = data.data.total.recovered;
    this.total.deaths = data.data.total.deaths;
  }
}
