import { Injectable, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';

import { map } from 'rxjs/operators';
import { Observable, BehaviorSubject, Subject } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class DataserviceService implements OnInit {

  data: any;
  state: string;
  district: string;
  BannerData: BehaviorSubject<any> = new BehaviorSubject('');
  districtdata: Subject<any> = new Subject<any>();

  private url_statewise: string = "https://api.rootnet.in/covid19-in/unofficial/covid19india.org/statewise";
  private url_dailycases: string = 'https://api.rootnet.in/covid19-in/unofficial/covid19india.org/statewise/history';
  private ulr_districtwise: string = "https://api.covid19india.org/state_district_wise.json";
  private url_banner: string = "https://api.covid19india.org/website_data.json";

  constructor(private http: HttpClient) { }

  ngOnInit(): void {
    this.getStateWiseData();
    this.getDailyCaseStatusData();
  }

  getBannerData(): Observable<any> {
    return this.http.get(this.url_banner);
  }

  getStateWiseData(): Observable<any> {
    return this.http.get(this.url_statewise)
      .pipe(
        map(data => {
          return data;
        })
      );
  }

  getDailyCaseStatusData(): Observable<any> {
    return this.http.get(this.url_dailycases)
      .pipe(
        map(data => {
          return data;
        })
      );
  }

  getState(state) {
    this.state = state;
  }

  getDistrictWiseData(state) {
    this.http.get(this.ulr_districtwise)
      .subscribe(data => {
        this.data = data
        this.district = this.data[this.state].districtData
        this.districtdata.next(this.district)
      })
  }
}
