import { Component, OnInit, ViewChild, Pipe, PipeTransform } from '@angular/core';
import { DataserviceService } from 'src/app/dataservice.service';
import { Covidcase } from 'src/app/interfaces/covidcase';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import { MatPaginator } from '@angular/material/paginator';
import { Covidtrack } from 'src/app/interfaces/covidtrack';

@Component({
  selector: 'app-statewise',
  templateUrl: './statewise.component.html',
  styleUrls: ['./statewise.component.css']
})
export class StatewiseComponent implements OnInit {

  constructor(private dataService: DataserviceService) { }

  dataSource: MatTableDataSource<Covidtrack>;
  displayedColumns: string[] = [
    'state',
    'confirmedChange',
    'confirmed',
    'recoveredChange',
    'recovered',
    'deathsChange',
    'deaths',
    'activeChange',
    'active'
  ]

  toDay: Covidcase[] = [];
  yesterDay: Covidcase[] = [];
  changes: Covidtrack[] = [];

  @ViewChild(MatPaginator, { static: true })
  paginator: MatPaginator;

  @ViewChild(MatSort, { static: true })
  sort: MatSort;

  ngOnInit() {

    this.dataService.getDailyCaseStatusData()
      .subscribe(
        data => {
          this.setDifferenceData(data);
        }
      )
  }

  setDifferenceData(data: any) {
    let history = data.data.history;
    let lastDay = history.length - 1;
    let today = history.length;
    let count = 1;
    history.forEach(item => {
      if (count == today) {
        this.toDay = item.statewise;
      }
      if (count == lastDay) {
        this.yesterDay = item.statewise;
      }
      count++;
    });

    this.toDay.forEach(data1 => {
      this.yesterDay.forEach(data2 => {
        if (data1.state == data2.state) {
          this.changes.push(
            {
              state: data1.state,
              confirmed: data1.confirmed,
              active: data1.active,
              recovered: data1.recovered,
              deaths: data1.deaths,
              confirmedChange: data1.confirmed - data2.confirmed,
              activeChange: data1.active - data2.active,
              recoveredChange: data1.recovered - data2.recovered,
              deathsChange: data1.deaths - data2.deaths
            }
          )
          this.dataSource = new MatTableDataSource(this.changes);
          this.dataSource.paginator = this.paginator;
          this.dataSource.sort = this.sort;
        }
      })
    })
  }

  applyFilter($event) {
    this.dataSource.filter = $event.target.value.trim();
    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }
}
